body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
#CEE5F2
#ACCBE1
#7C98B3
#637081
#536B78
 */

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    background: #CEE5F2;
}

header {
    height: 100vh;
}

input {
    background: #ACCBE1;
    height: 30px;
    padding: 4px;
    border-radius: 5px;
}

input:focus {
    background: white;
}

.myApp {
    width: 90vw;
    margin: 0 auto;
}

.dataDisplay {
    width: 90vw;
    margin: 2vh auto;
    height: 80vh;
}

.mapboxgl-ctrl-attrib-inner {
    display: none;
}

.marker {
    width: 30px;
    height: 30px;
}

.markerContainer {
    background: rgba(255, 255, 255, 0.8);
    border: 3px solid black;
    padding: 3px;
}

.markerContainer:hover {
    border: 3px solid white;
}

.controls {
    background: #536B78;
    /*width: 400px;*/
    margin: 2vh auto;
    padding: 20px;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.controlForm {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.formInput {
    font-size: 16px;
    padding: 2px;
}

.icon {
    width: 40px;
    height: 40px;
    border: 3px solid black;
    background: #CEE5F2;
    border-radius: 5px;
    padding: 5px;
    cursor: pointer;
}

.icon:hover {
    border: 3px solid white;
}

.infoCardContainer {
    background: #7C98B3;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    width: 90vw;
    margin: 2vh auto;
    height: 100vh;
    overflow-y: scroll;
}

.infoCard {
    background: #ACCBE1;
    border-radius: 10px;
    width: 300px;
    padding: 20px;
    margin: 20px;
}

.softShadow {
    box-shadow: 0 0 30px 2px rgba(0,0,0,0.75);
}

.barForChart:hover {
    fill: #e5e5ff;
}
